
.new-post{
    background-color: white;
    width: 45rem;
    margin: auto;
    display: flex;
    flex-direction: column;
    border: 1px solid rgb(175, 175, 175);
    padding: 1.5rem;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    z-index: 150;
    border-radius: .5rem;
    backdrop-filter: blur(5px);
    background: rgba(255,255,255,0.25);
    box-shadow: 10px 10px 20px -10px rgba(255, 255, 255, 0.3);
    color: white;
    border-right: 1px solid rgba(255,255,255,0.2);
    border-bottom: 1px solid rgba(255,255,255,0.2);
    border-radius: 1.5rem;
    @media only screen and (max-width:50em) {width: 35rem;}
    &-btn{
        &:disabled{
            background-color: gray;
            cursor: not-allowed;
            &:hover{
                border:2px solid gray;
                color: white;
            }
        }
    }
    &-top{
        display: flex;
        align-items: center;
        justify-content: center;
        padding-bottom: 1rem;
        border-bottom: 1px solid rgb(175, 175, 175);

    }
    &-body{
        display: flex;
        flex-direction: column;

    }
    &-caption{
        display: flex;
        input{
            padding: 1.5rem 2rem;
            margin-bottom: 1rem;
            width: 100%;
            border: 1px solid transparent;
            border-radius: 4px;
            background-color: rgba(209, 209, 209, 0.26);
            outline: none;
            transition: all .2s;
            font: inherit;
            &:focus{
                border: 1px solid rgba(255,128,177,1) ; 
            }
            &::-webkit-input-placeholder{
                color: rgba(255, 255, 255, 0.575);
            }
        }
    }
    &-upload-photo{
        display: flex;
        flex-direction: column;
        align-items: center;
        position: relative;
        min-height: 30rem;
        img{
            width: 100%;
            margin: 1rem 0;
            border-radius: 4px;
        }
    }
}

.file-btn {
    padding: 2rem;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 10;
    label{ background-color: rgba(0,6,82,1); color: white;}
    input{
        width: 0.1px;
        height: 0.1px;
        opacity: 0;
        overflow: hidden;
        z-index: -1;
    }
}

.modal-overlay {
    z-index: 99;
    background-color: rgba(0, 0, 0, 0.7);
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
}

@keyframes shake{
	0%{transform: rotate(0deg);}
	25%{transform: rotate(10deg);}
	50%{transform: rotate(0deg);}
	75%{transform: rotate(-10deg);}
	100%{transform: rotate(0deg);}
}

