.edit-profile{
    background-color: white;
    width: 45rem;
    margin: auto;
    display: flex;
    flex-direction: column;
    border: 1px solid rgb(175, 175, 175);
    padding: 1.5rem;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    z-index: 150;
    backdrop-filter: blur(5px);
    background: rgba(255,255,255,0.25);
    box-shadow: 10px 10px 20px -10px rgba(255, 255, 255, 0.3);
    color: white;
    border-right: 1px solid rgba(255,255,255,0.2);
    border-bottom: 1px solid rgba(255,255,255,0.2);
    border-radius: 1.5rem;
    @media only screen and (max-width:50em) {width: 35rem;}
    &-top{
        display: flex;
        align-items: center;
        justify-content: center;
        padding-bottom: 1rem;
        border-bottom: 1px solid rgb(175, 175, 175);

    }
    &-body{
        display: flex;
        flex-direction: column;

    }
    
    &-photo{
        display: flex;
        flex-direction: column;
        align-items: center;
        position: relative;
        img{
            width: 100%;
        }
    }
    &-upload-photo{ 
        margin-top: 3rem;
        display: flex;
        flex-direction: column;
        align-items: center;
        position: relative;
    }
    &-img-box{
        width: 18rem;
        height: 18rem;
        overflow: hidden;
        border-radius: 50%;
        margin: 1rem;
        position: relative;
        img{
            width: 100%;
        }
    }
    &-info-box{
        display: flex;
        flex-direction: column;
        padding: 4rem;
    }
    &-info{
        display: flex;
        flex-direction: column;
        padding: 1rem 0;
        label{color: rgb(194, 194, 194);}
        input{
            border: none;
            border-bottom: 2px solid #9C518C ;
            margin-bottom: 3rem;
            padding-top: .5rem;
            outline: none;
            color: inherit;
            font: inherit;
            background-color: transparent;
            &:focus{
                border-bottom: 2px solid rgba(255,128,177,1) ;
            }
        }
    }
}

.edit-btn {
    padding: 1rem;
    position: absolute;
    top: 12rem;
    right: 25%;
    z-index: 10;
    label{
        background-color: #9C518C ; 
        color:white; border: none; 
        font-size: 1.8rem;
        line-height: 0; 
        padding: 1.5rem;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    input{
        width: 0.1px;
        height: 0.1px;
        opacity: 0;
        overflow: hidden;
        z-index: -1;
    }
}