.sign-in-page{
    min-height: 100vh;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    background-image: url('../../images/bg4.jpg');
    background-position: center;
    background-size: cover;
    h1{
        text-align: center;
        color: #b5d2ff;
        font-weight: bolder;
    }
}
.sign-in{
    height: 100%;
    width: 100%;
    margin: auto;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap-reverse;
    padding-top: 5.5rem;
    &-message{
        color: red;
    }
    &-btn{
        width: 60%;
        margin: auto;
        text-align: center;
    }
    &-form{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: 2.5rem;
        max-width:30rem;
        background: rgba(255, 255, 255, 0.514);
        backdrop-filter: blur(2px);
        background: rgba(255,255,255,0.2);
        box-shadow: 10px 10px 20px -10px rgba(255, 255, 255, 0.3);
        color: rgba(255, 255, 255, 0.884);
        border-right: 1px solid rgba(255,255,255,0.2);
        border-bottom: 1px solid rgba(255,255,255,0.2);
        border-radius: 1.5rem;

        a{
            color: #b5d2ff;
        }
        
        &-title{
            text-align: center;
            color: #FF8BB2;
            padding: 1.5rem;
            padding-top: .5rem;
        }
        &-fields{
            margin:1rem auto;
            padding: 1rem .5rem;
            width: 80%;
            input {
                box-sizing: border-box;
                border: 2px solid transparent;
                outline:none;
                background: none;
                background-color: #e3f0ff4f;
                color: rgb(224, 224, 224);
                padding:1rem 3rem;
                font-size: inherit;
                border-radius: .5rem;
                transition: all .2s;
                &:focus{
                    border: 2px solid rgb(192, 224, 255);
                }
                &::placeholder{
                    color: rgb(216, 216, 216);
                }
              }
        
        }
        &-field{
            margin-bottom: 3rem;
            border-radius: .5rem;
            
        }
        
    }
}



.sign-in-to-sign-up{
    text-align: center;
    padding: 2rem;
    &-link{
        text-decoration: underline;
        color: #324d6d;
        
    }
}
